@import "../../var";

.about-container {
  height: 100vh;
  padding: 7vh 10vh;
  overflow: hidden;
  align-items: center;
  text-align: left;

  .about-header {
    padding-bottom: 2rem;
    font-size: 36px;
    font-family: $font-primary;
    color: $text-primary;
    font-weight: 400;
    letter-spacing: 2px;

    &::before {
      counter-increment: section 1;
      content: "0" counter(section) ".";
      font-family: $font-secondary;
      color: $secondary-color;
      font-size: 27px;
      letter-spacing: -0.05em;
      padding-right: 0.5em;
    }

    &::after {
      content: "";
      display: block;
      width: 240px;
      height: 1px;
      margin: 5px;
      background-color: $secondary-color;
    }
  }

  .about-content {
    display: flex;
    .about-inner {
      padding-right: 5px;

      .about-paragraph {
        color: $text-primary;
        font-family: $font-primary;
        font-size: 16px;
        font-weight: 300;
        padding-bottom: 1rem;
        letter-spacing: 0.5px;

        a {
          cursor: pointer;
          color: $secondary-color;
          text-decoration: none;
        }
      }

      .stack-list {
        display: grid;
        grid-template-columns: repeat(3, 1fr);
        gap: 5px 10px;
        list-style: none;
        color: $text-primary;
        font-weight: 100;
        font-family: $font-secondary;
        font-size: 13px;

        li {
          margin: 8px 0;

          &::before {
            content: "Â»";
            padding-right: 10px;
            color: $secondary-color;
          }
        }
      }
    }
    .profile-image {
      padding: 0 10px;
      height: 40vh;

      box-shadow: 0px 0px 30px -15px #129b2e;

      &:hover {
        box-shadow: 0px 0px 30px -5px #129b2e;
      }
    }
  }

  @include media-sm {
    .about-header {
      padding-bottom: 0.5rem;
      font-size: 18px;
      letter-spacing: 0.5px;

      &::before {
        font-size: 14px;
      }

      &::after {
        width: 120px;
        margin: 2px;
      }
    }

    .about-content {
      .about-inner {
        .about-paragraph {
          font-size: 10px;
          padding-bottom: 0.5rem;
          letter-spacing: normal;
        }
        .stack-list {
          gap: 0px 3px;
          font-size: 10px;

          li {
            margin: 4px 0;

            &::before {
              padding-right: 5px;
            }
          }
        }
      }
      .profile-image {
        position: relative;
        height: 15vh;
        top: 450px;
        right: 160px;
      }
    }
  }

  @include media-md {
    .about-content {
      .profile-image {
        height: 20vh;
      }
    }
  }

  @include media-lg {
    .about-content {
      .profile-image {
        height: 25vh;
      }
    }
  }
}
